<script>
import MyData from "@/components/dashboard/UserMyData.vue"
import EditData from "@/components/dashboard/EditMyData.vue"
export default {
    name: "UserDashboard",
    components: {
        MyData,
        EditData
    },
    data() {
        return {
            datas: {
                "id": null,
                "email": null,
                "name": null,
                "nickname": null,
                "phone_number": null,
                "about_sirkel": null,
                "major": null,
                "answer": null,
                "date_birth": null,
                "address": null,
                "education": null,
                "cv": null,
                "roles": null,
                "created_at": null,
                "updated_at": null,
                "photo": null
            },
            rerender: 0,
            rerender2: 0,
            inputFile: null,
            profpicPreview: this.picturl,
            profpicModal: false,
            edit: false,
        }
    },
    beforeCreate() {
        this.profpicModal = false;
        const applyconfig = {
                    scroll: 0
                }
        this.$emit('nav-scroll', applyconfig);

        let jwttoken = (this.$session.has('token') ? this.$session.get('token') : window.$cookies.get('token'));
        console.log('Search For Data... ' + jwttoken); 

        this.axios.get(process.env.VUE_APP_API_URL + "/api/freelancer-data", { headers: {"Authorization" : `Bearer ${jwttoken}`} })
        .then(response => {
            this.datas = response.data.freelances[0];
            this.datas.roles = this.datas.roles.replace(/,/g, ", ");
            console.log(response.data);
        })
        .catch(err => console.log(err));

    },
    methods: {
        openModal() {
            this.profpicPreview = this.picturl;
            this.profpicModal = true;
            this.$bvModal.show('change-profpic-modal');
        },
        removeProfPic() {
            const data = new FormData();
            data.append('photo', 'Post request body');
            let jwttoken = (this.$session.has('token') ? this.$session.get('token') : window.$cookies.get('token'));
            this.axios.post(process.env.VUE_APP_API_URL + "/api/avatar-delete", data, { headers: {"Authorization" : `Bearer ${jwttoken}`} })
            .then(response => {
                console.log(response.data);
                this.$bvModal.hide('change-profpic-modal');
                this.profpicModal = false;
                window.location.href = "/dashboard";
            })
            .catch(err => console.log(err))
        },
        changeProfPic() {
            this.$refs.fileInput.click();
            console.log("Ceritanya, mau ganti foto");
        },
        toggleEdit() {
            //bisa diuat fungsi untuk fadeout-fadein components nanti
            this.edit = !this.edit;
            //bisa dibuat fungsi smoothscroll Y ke minimal Ynya komponen tabs nanti
            window.scrollTo(0,0);
        },
        saveAction(){
            const data = new FormData();
            let jwttoken = (this.$session.has('token') ? this.$session.get('token') : window.$cookies.get('token'));
            data.append('photo', this.inputFile);
            this.axios.post(process.env.VUE_APP_API_URL + "/api/pict-freelancer", data, { headers: {"Authorization" : `Bearer ${jwttoken}`} })
            .then(response => {
                console.log(response.data);
                this.$bvModal.hide('change-profpic-modal');
                this.profpicModal = false;
                window.location.href = "/dashboard";
            })
            .catch(err => console.log(err))

        },
        changeImage (e){
            this.inputFile = e.target.files[0];
            var reader = new FileReader();
            reader.onload = (e) => {
                let contents = e.target.result
                this.profpicPreview = contents
            }
            reader.readAsDataURL(e.target.files[0]);
        },
    },
    computed: {
        picturl: function () {
                if (this.datas.photo == null){
                    return process.env.VUE_APP_API_URL+"/images/freelancepage/default.jpg"
                }else{
                    let new_photo = (this.datas.photo.split("public").pop())
                    return process.env.VUE_APP_API_URL+new_photo
                }
            }
    },
}
</script>

