                ></b-form-file> -->
                <div class="mb-3">
                    <div class="label">My CV</div>
                        <b-form-file
                        v-model="file"
                        :state="Boolean(file)"
                        placeholder="Choose a file or drop it here..."
                        drop-placeholder="Drop file here..."
                        ></b-form-file>
                    <div class="value value-file">{{this.users.cv}}</div>
                </div>
