<script>
export default {
    data () {
        return {
            selected_role: [],
            role_list: [],
            error: ""
        }
    },
    mounted() {
        this.axios.get(process.env.VUE_APP_API_URL + "/api/rolee_published")
        .then(response => {
            this.role_list = response.data;
            console.log(this.role_list)
        })
        .catch(error => console.log(error));
    },
    methods: {
        edit() {
            const data = new FormData();
            data.append('nickname', this.users.nickname);
            data.append('name', this.users.name);
            data.append('email', this.users.email);
            data.append('phone_number', this.users.phone_number);
            data.append('address', this.users.address);
            //data.append('cv', this.file);
            //data.append('freelance_role_id', [1,2,3]);
            data.append('education', this.users.education)
            data.append('city', this.users.city);
            data.append('country', this.users.country);
            data.append('account_number', this.users.account_number);
            this.axios
                .post(process.env.VUE_APP_API_URL + "/api/v1/account/freelancer/" + this.users.id, 
                data, 
                {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
                })
                .then(response => {
                    if (response.status == 200) {
                        //submit_button.innerHTML = "SUBMIT";
                        this.showSuccess();
                    } else {
                        //submit_button.innerHTML = "SUBMIT";
                        this.showError(response.data);
                    }
                })
                .catch(error => {
                    let modalMessage = "";
                    for (let message in error.response.data.errors) {
                        modalMessage += error.response.data.errors[message][0] + "<br>";
                }
                //submit_button.innerHTML = "SUBMIT";
                this.showError(modalMessage);
                });
            console.log(this.selected_role);
        },
        toggleEdit() {
            //tell parent to execute methods
            this.$parent.edit = this.$emit('toggleEdit');
        },
        showError(message) {
        this.error = message;
        this.$bvModal.show("modal-failed");
        },
        showSuccess(){
        this.error = "Edit Success!";
        this.$bvModal.show("modal-success");
        },
        closeSuccess(){
        this.$bvModal.hide("modal-success");
        this.$parent.edit = this.$emit('toggleEdit');
        //this.$router.push('/');
        },
        closeError() {
        this.$bvModal.hide("modal-failed");
        },
    },
    props: ['users'],
    
}
</script>

